@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  font-family: poppins,sans-serif;
  font-size: 1.1rem
}

.bg-primary {
  background-color: #c3dd32!important
}

.bg-secondary {
  background-color: #ff9a00 !important;
}

.bg-accent {
  background-color: #ff9a00!important;
  height: max-content;
}

.text-primary {
  color: #c3dd32!important
}

.text-secondary {
  color: #284155!important
}

.text-accent {
  color: #ff9a00!important
}
.term_links ul{margin-bottom: 0;}
.social_media_footer img {
  width: 20px;
}
.btn {
  transition: 500ms all;
  border-radius: 5px !important;
  border: transparent;
}

.btn-primary {
  background: #c3dd32;
  border-color: #c3dd32;
  color: #284155
}

#hero-form .bg-accent.py-4.px-2.text-center.text-secondary {
  background: #fff !important;
}

.btn-primary:hover,.btn-primary:focus,.btn-primary:active,.btn-primary:active:focus {
  background: #c3dd32;
  border-color: #c3dd32;
  color: #284155;
  box-shadow: 1px 1px 6px 2px rgba(0,0,0,.3)
}

.btn-secondary {
  background: #000000;
  border-color: #000000;
  color: #fff;
  border-radius: 5px !important;
}

.btn-secondary:hover,.btn-secondary:focus,.btn-secondary:active,.btn-secondary:active:focus {
  background: #000000;
  border-color: #000000;
  color: #fff;
  box-shadow: 1px 1px 6px 2px rgba(0,0,0,.3)
}

.btn-outline-primary {
  border-color: #c3dd32
}

.btn-outline-primary:hover,.btn-outline-primary:focus,.btn-outline-primary:active,.btn-outline-primary:active:focus {
  background-color: #c3dd32;
  border-color: #c3dd32;
  color: #284155!important
}

.btn-outline-secondary {
  border-color: #284155!important;
  color: #284155!important
}

.btn-outline-secondary:hover,.btn-outline-secondary:focus,.btn-outline-secondary:active,.btn-outline-secondary:active:focus {
  background-color: #284155!important;
  border-color: #284155!important;
  color: #fff!important
}

.btn-accent {
  background: #ff9a00;
  border-color: #ff9a00;
  color: #fff
}

.btn-accent:hover,.btn-accent:focus,.btn-accent:active,.btn-accent:active:focus {
  background: #ff9a00;
  border-color: #ff9a00;
  color: #fff;
  box-shadow: 1px 1px 6px 2px rgba(0,0,0,.3)
}

.white-fill {
  fill: #fff
}

a {
  text-decoration: none
}

@media(min-width: 992px) {
  .md-py-10 {
      padding-top:8rem!important;
      padding-bottom: 8rem!important
  }

  .md-pt-10 {
      padding-top: 8rem!important
  }
}
header.bg-accent {
  background: transparent !important;
  position: absolute;
  left: 0px;
  right: 0px;
  width: 100%;
  padding: 0px 30px;
  z-index: 1;
}
.App .header-search-icon a {
  background: #ff9a00;
  padding: 12px 30px;
  font-weight: 500;
  line-height: 23px;
}
.App header {
  background: #284155
}

.App header li a.nav-link {
  font-weight: 500;
  color: #fff
}

.App header li svg {
  max-width: 40px;
  height: auto
}

#hero {
  background: #000 url(./assets/images/hero-bg.jpg) center center no-repeat;
  background-size: cover;
  color: #fff;
  padding-top: 11rem !important;
}

#hero-trustlogos img {
  max-height: 70px
}

#hero-form {
  position: relative;
  max-width: 450px;
  margin: auto
}

#platforms-slider {
  max-height: 150px;
  overflow: hidden
}

#platforms-slider img {
  max-height: 50px;
  margin: auto
}

ul.checklist {
  list-style: none;
  margin: 0 0 20px;
  padding: 0
}

ul.checklist li:before {
  content: "";
  background: url(./assets/images/tick.svg) center left no-repeat;
  background-size: contain;
  width: 20px;
  height: 25px;
  position: absolute;
  left: 0;
  top: 0
}

ul.checklist li {
  position: relative;
  padding-left: 30px;
  padding-bottom: 5px
}

@media(min-width: 992px) {
  .cta-img {
      position:absolute;
      top: 50%;
      transform: translateY(-50%)
  }
}

#how {
  background-color: #f2f2f2;
  color: #000
}

#how h2 {
  font-size: 3rem;
  font-weight: 700
}

#steps-slider {
  margin-left: 2rem;
  margin-right: 2rem
}


.step {
  background: #fff;
  box-shadow: 6px 6px 6px 0 #848c93;
  padding: 3rem 5.2rem 3rem 1rem;
  position: relative;
  min-height: 430px
}

.step h3 {
  color: #ff9a00;
  font-size: 1.4rem
}

.step p {
  color: #b4afa9
}

.step-number {
  position: absolute;
  right: 2rem;
  width: 3rem;
  text-align: right
}

.step-number svg {
  height: auto
}


.cls-1 {
  fill: #ff9a00;
}

#quote-form {
  display: flex;
    justify-content: center;
    padding: 1rem;
}

.step-number:after {
  background: #e1e1e1;
  content: "";
  width: 1px;
  height: 80px;
  position: absolute;
  top: 110%;
  left: 50%
}

#testimonials-slider {
  padding-top: 5rem;
  margin-left: 2rem;
  margin-right: 2rem
}

.testimonial-img {
  box-shadow: 20px 10px 6px rgba(0,0,0,.2);
  margin: auto;
  border: 1px solid rgba(0,0,0,.2);
  margin-bottom: 30px;
  max-width: 85%
}

footer {
  font-size: 1rem;
  color: #fff
}

footer a {
  color: #fff
}

footer ul {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 1.1rem
}

footer svg {
  max-height: 40px
}

.slick-arrow {
  background: 0 0;
  position: absolute;
  top: 50%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  text-indent: -9999em;
  width: 2rem;
  height: 2rem;
  border: 0;
  transform: translateY(-50%);
  cursor: pointer
}

.slick-prev {
  background-image: url(./assets/images/arrow-left.png);
  left: -2rem
}

.slick-next {
  background-image: url(./assets/images/arrow-right.png);
  right: -2rem
}

.slick-slide .mx-3 {
  overflow: hidden;
}

#social-links {
  list-style: none;
  padding: 0;
  margin: 0
}

#social-links li {
  display: inline-block;
  margin: 0 3px
}

#social-links svg {
  fill: #fff;
  height: 25px
}

.lp-modal .banner-form-lp {
  width: 450px;
  max-width: 100%
}


/*Footer Section Begin*/

footer {
background: #000;
padding: 80px 0;
position: relative;
}

