.social_media_footer img {
filter: none !important;
margin: 0px !important;
transition: 0.4s ease;
}

.social_media_footer a:hover img {
filter: brightness(0) invert(1) !important;
}
.logo_footer_sec img {
width: 49%;
margin-bottom: 10px;
}

.logo_footer_sec p {
font-size: 17px;
line-height: 30px;
color: var(--white);
font-weight: 300;
}

.social_media_footer ul li a {
background: #fff;
width: 40px;
height: 40px;
display: inline-block;
border-radius: 50px;
display: flex;
align-items: center;
justify-content: center;
font-size: 20px;
/* display: flex; */
/* align-items: center; */
/* justify-content: center; */
/* text-align: center; */
}

.social_media_footer ul li {
display: inline-block;
margin: 0 15px 0 0;
}

.social_media_footer {
padding-top: 20px;
display: inline-block;
}

.social_media_footer ul li a:hover {
background: #ff9a00;
color: #000;
}

.heading_footer h3 {
color: var(--white);
font-size: 40px;
margin-bottom: 30px;
display: inline-block;
}

.footer_menu_sec ul li a {
color: var(--white);
font-size: 15px;
padding-bottom: 15px;
display: inline-block;
position: relative;
padding-left: 25px;
}

.footer_menu_sec ul li a:after {
content: "";
position: absolute;
border: 2px solid var(--primary_color);
left: 0;
top: 3px;
width: 18px;
height: 18px;
border-radius: 50px;
}

.copy__right {
background: #fe9603;
padding: 15px 0;
}

.copy_text p {
color: var(--white);
margin-bottom: 0;
font-size: 16px;
font-weight: 300;
}

.term_links ul li {
display: inline-block;
padding: 0 0 0 25px;
}

.term_links {
text-align: right;
}

.term_links ul li a {
font-size: 16px;
color: var(--white);
font-weight: 300;
}

.badge_logo ul li img {
width: auto;
filter: unset;
margin-bottom: 0;
}

.badge_logo ul li {
display: inline-block;
padding: 0 20px 0 0;
}

.badge_logo ul {
display: flex;
align-items: center;
}

.badge_logo ul li a {
display: inline-block;
}

.footer__info__detail ul li {
position: relative;
margin-bottom: 20px;
padding-left: 30px;
}

.footer__info__detail ul li a {
font-size: 17px;
color: var(--white);
font-weight: 500;
}

.footer__info__detail ul li span {
position: absolute;
left: 0;
top: 5px;
}

.footer__info__detail ul li p {
font-size: 16px;
color: var(--white);
font-weight: 500;
line-height: 22px;
}

.footer__info__detail ul li a:hover {
color: var(--primary_color);
}

.footer_menu_sec ul li a:hover {
color: var(--primary_color);
}

.term_links ul li a:hover {
color: var(--primary_color);
}

/*Footer Section End*/

